$BackColor: #2B2B2B;
$HeaderColor: #191919;
$Font: 'Roboto', sans-serif;
$FontColor: #EFEFEF;
$desktop-max: 1920px;
$desktop-min: 1025px;
$tablet-max: 1024px;
$tablet-min: 768px;
$phone-max: 740px;
$phone-min:320px;

body {
    margin: 0 auto;
    padding: 0;
    width: 100%;
    height: 100%;
    background-color: $BackColor ;
}

.container {
    margin: 0 auto;
    padding: 0;
    width: 90%;
    max-width: 1920px;
   
    text-align: center;

}
   

.table {
    margin: 0 auto;
    padding: 0;
    width: 100%;
    
      
}


.col-1 {
            width: 8.33333333333%;

        }

        .col-2 {
            width: 16.6666666667%;
        }
        .col-3 {
            width:25%;
        }
        .col-4 {
            width: 33.3333333333%;

        }
        .col-5 {
            width: 41.6666666667%;
        }
        .col-6 {
            width:50%;
        }
        .col-7 {
            width: 58.3333333333%;
        }
        .col-8 {
            width:66.6666666666%;
        }
        .col-9 {
            width:75%;
        }
        .col-10 {
            width: 83.3333333333%;
        }
        .col-11 {
             width:91.6666666666%; 
        }
        .col-12 {
             width: 100%;
        }

        [class*="col-"] {
          float:left; 
        }

        [class*="col-"]:last-of-type {
        }

        .row:before
            :after
        {
          clear: both;
          content:"";
        } 



.menu{
	display: none;
	width:50px;
	height: 40px;
    padding:1em; 
    
    
}
	
.menu:hover{
	cursor:pointer
}
.menu li{
	list-style:none;
	margin:5px 0;
	width:50px;
	height:8px;
	background:#949494;
}
.on{
	transform:rotate(90deg);
}
.on li{
	background:#ddd
}

nav{
    background-color: $BackColor;
    text-align:right;
    width: 100%;
    
    
    
}
	
nav ul li{
	display:inline-block;
    line-height: 2;
    text-align: center;
}


  
nav a{
	display:block;
	font-family: $Font;
    font-size: 100%;
    text-decoration: none;
    color: $FontColor;
    margin-left: 15%;
}
nav a:hover{
	color:#999
}
a, .menu, .menu li{
	
	transition: all 0.3s ease-out
}

	
#logo-holder {
    
    margin: 0 auto;
    padding: 0;
    position: relative;
    top:  25%;
    left:0%;
    
    .logo {
        margin: 0;
        padding: 0;
        width: 100%;
    }
}


#text {
    margin: 0 auto;
    padding: 0;
    position: relative;
    text-align: center;
    padding-top: 10%;
    left:2%;
    
    h1{
        font-family: $Font;
        font-size: 150%;
        color: $FontColor;
    }
    
    p{
        font-family: $Font;
        font-size: 130%;
        color: $FontColor;
        text-align: justify;
        
    }
}
#links {
    margin: 0 auto;
    padding: 0;
    padding-top: 5%;
    text-align: right;
    
    
    .media-links {
        margin: 0 auto;
        padding: 0;
        width:2%;
        margin-left: 2%;
        
    }
}

/* Tablet Portrait */

@media (max-width: 768px) {
    .container {
        margin: 0 auto;
        padding: 0;
        width: 100%;
        max-width: 768px;
          
         .menu{
			display:block;
    	
		}
        nav{
			width: 100%;
			display:none;
			background:$BackColor;
            text-align: center;
            padding: 0;
            padding-bottom: 2%;
            position: relative;
            right: 2%;
		}
        nav ul li{
			display:block;
			border-bottom:1px solid #ccc;
            
		}
        nav ul li:last-child{
            border:none
        }
        
         nav a{
            font-size: 140%;
             position: relative;
            right: 8%;
        }  
        
        #logo-holder {
            width: 80%;
            left: 10%;
            
            .logo {
                width: 90%;
            }
        }
           #text {
               width: 80%;
               left: 10%
           }
        #links {
            width: 90%;
        
            .media-links {
                 width: 5%;
            }
        }
    }   
}

